
import Alert from 'vux/src/components/alert/index.vue'


let resolve

export default {
components: {
Alert
},
data () {
return {
show: false,
title: '',
content: ''
}
},
created () {
window.sdk.alert = async ({ title = '', content = '' }) => {
this.show = true
this.title = `${title}`
this.content = `${content}`
let promise
({ promise, resolve } = window.util.promise())
await promise
}
},
methods: {
hide () {
resolve()
}
}
}



