
import Loading from 'vux/src/components/loading/index.vue'


export default {
components: {
Loading
},
data () {
return {
show: false,
text: ''
}
},
created () {
window.sdk.loading = async ({ show, text = '' }) => {
this.show = !!show
this.text = `${text}`
}
}
}



