
import Masker from 'vux/src/components/masker/index.vue'


export default {
components: {
Masker
},
data () {
return {
show: false,
color: '',
opacity: '',
fullscreen: false
}
},
created () {
window.sdk.masker = ({ show, color, opacity, fullscreen }) => {
this.show = !!show
this.color = `${color}`
this.opacity = +opacity || 0
this.fullscreen = !!fullscreen
}
}
}



