
import Actionsheet from 'vux/src/components/actionsheet/index.vue'


let resolve

export default {
components: {
Actionsheet
},
data () {
return {
show: false,
menus: []
}
},
created () {
window.sdk.actionsheet = async ({ menus }) => {
this.menus = menus
this.show = true
let promise
({ promise, resolve } = window.util.promise())
const result = await promise
return result
}
},
methods: {
click (index) {
if (index === 'cancel') {
resolve({
index: -1
})
return
}
const value = this.menus[index]
resolve({
index,
value
})
},
close () {
resolve({
index: -1
})
}
}
}



