
import Toast from 'vux/src/components/toast/index.vue'


const { _ } = window

export default {
components: {
Toast
},
data () {
return {
list: []
}
},
created () {
window.sdk.toast = ({ text = '', width = '20em' }) => {
const item = {}
item.text = `${text}`
item.width = `${width}`
item.id = this.$.now
item.disabled = false
this.list.push(item)
}
window.util.loop(async () => {
await window.util.sleep(60 * 1000)
if (this.list.length) {
const bool = _.every(this.list, 'disabled')
if (bool) {
this.list = []
}
}
})
},
methods: {
async remove (item) {
item.disabled = true
}
}
}



